a,
body a:where(:not(.wp-element-button)) {
  color: #777777;
  text-decoration: none;
  outline: none;
  transition: 0.2s ease-in-out;
}
a:hover,
body a:where(:not(.wp-element-button)):hover {
  text-decoration: underline;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Signika", sans-serif;
  font-weight: 600;
  color: #005f58;
}

.bold {
  font-family: Signika;
  font-weight: 700;
}

.reg {
  font-family: Signika;
  font-weight: 400;
}

.header__top {
  position: relative;
  padding: 5px 15px 8px;
  font-family: Signika;
  font-size: 20px;
  line-height: normal;
  color: #005f58;
  text-align: center;
  background-color: #ffffff;
}
.header__top .phone-no {
  position: absolute;
  top: 8px;
  right: 15px;
  font-style: normal;
  font-size: medium;
  color: #005f58;
}
.header__top .phone-no img {
  width: 15px;
}
.header__middle {
  background-color: #005f58;
}
.header__middle-wrap {
  display: flex;
  height: 80px;
}
.header__logo img {
  margin-top: 15px;
  max-width: 158px;
  height: auto;
}
.header__links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  width: 60%;
  height: 100%;
  font-family: Signika;
  color: #ffffff;
}
.header .account-details a {
  margin: 0 8px;
  font-size: 18px;
  color: #ffffff;
}
.header__cart {
  position: relative;
}
.header__cart-count {
  position: absolute;
  right: -5px;
  top: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 15px;
  height: 15px;
  font-size: 11px;
  font-weight: 700;
  color: #ffffff;
  background-color: #eb8f2e;
  border-radius: 50%;
}
.header__search {
  position: relative;
}
.header__search .search-field {
  padding: 3px 0.75rem 3px 35px;
  max-width: 240px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  border-radius: 10px;
  border: 0;
  outline: 0;
  -webkit-appearance: none;
}
.header__search .search-field::-webkit-input-placeholder {
  color: #005f58;
}
.header__search .search-field:-moz-placeholder {
  color: #005f58;
}
.header__search .search-field::-moz-placeholder {
  color: #005f58;
}
.header__search .search-field:-ms-input-placeholder {
  color: #005f58;
}
.header__search .search-field::-webkit-search-cancel-button {
  -webkit-appearance: none;
}
.header__search .search-submit {
  position: absolute;
  top: 50%;
  left: 0;
  font-size: 22px;
  color: #005f58;
  background: transparent;
  border: 0;
  transform: translateY(-50%);
}
.header__bottom {
  background-color: #fff3eb;
}
.header__bottom #mega-menu-wrap-primary #mega-menu-primary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  width: 60%;
  min-height: 75px;
  font-family: Signika;
  font-weight: 700;
}

.footer__main {
  background-color: #f7f8fa;
}
.footer__main-title {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.6;
}
.footer__main-content ul {
  padding: 0;
  list-style: none;
}
.footer__main-content ul a {
  display: inline-block;
  padding: 3px 0 3px;
  font-size: 14px;
  transition: color 0.2s linear;
}
.footer__main-content ul a:hover {
  color: #ff8f00;
}
.footer__logo img {
  width: auto;
  max-height: 30px;
}
.footer__copyright {
  font-size: 14px;
  line-height: 22px;
}
@media screen and (min-width: 790px) {
  .footer__main {
    padding-bottom: 53px;
  }
  .footer__main-title {
    padding-bottom: 15px;
  }
  .footer__main div[class^=col-] {
    margin-top: 54px;
  }
  .footer__bottom {
    padding: 10px 0;
    color: #888888;
    background-color: #ffffff;
  }
  .footer__copyright {
    padding-top: 19px;
    padding-bottom: 7px;
  }
}

.vk-icon-banner {
  position: relative;
  padding: 50px 20px;
}
.vk-icon-banner__row {
  display: flex;
  justify-content: space-around;
}
.vk-icon-banner__col {
  display: flex;
  align-items: center;
}
.vk-icon-banner__col .icon {
  margin-right: 15px;
  width: 50px;
}
.vk-icon-banner__col .icon img {
  width: 100%;
  height: auto;
}
.vk-icon-banner__col .desc p {
  margin-bottom: 0;
}
.vk-icon-banner__col .desc p, .vk-icon-banner__col .desc a {
  color: #4d4d4d;
}
.vk-icon-banner__col .desc .title {
  font-size: 18px;
}
.vk-icon-banner:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: 0 auto;
  width: 40%;
  height: 2px;
  background-color: #b1aca8;
}
@media screen and (max-width: 1199px) {
  .vk-icon-banner__row {
    flex-flow: wrap;
  }
  .vk-icon-banner__col {
    padding: 0 7%;
    margin: 15px 0;
    width: 50%;
  }
}
@media screen and (max-width: 767px) {
  .vk-icon-banner__col {
    padding: 0 50px;
    width: 100%;
  }
}